import $ from "./lib/lib";

$("#first").add("click", () => {
    $("div").eq(1).fadeToggle(800);
});

$("[data-count='second']").add("click", () => {
    $("div").eq(2).fadeToggle(800);
});

$("button").eq(2).add("click", () => {
    $(".w-500").fadeToggle(800);
});

